<template>
	<nav>
		<ul>
			<li>
				<!-- 关键字exact实现点那个另外一个背景样式就消失 -->
				<router-link to="/" exact>博客</router-link>
				<router-link to="/add" exact>写博客</router-link>
			</li>
		</ul>
	</nav>
</template>

<script>
	export default{
		name:"blog-header"
	}
</script>

<style scoped>
ul{
	list-style: none;
	text-align: center;
	margin: 0;
}
li{
	display: inline-block;
	margin: 0 10px;
}
a{
	color: #fff;
	text-decoration: none;
	padding: 12px;
	border-radius: 5px;
}
nav{
	background: crimson;
	padding: 30px 0;
	margin-bottom: 40px;
}
.router-link-active{
	background: rgba(255,255,255,0.8);
	color: #444;
}
</style>



